import React from 'react';
import { useState, useEffect } from 'react';
import { Layout, Menu, theme, Space, Image, ConfigProvider, Popover, Divider } from 'antd';
import { Link } from 'react-router-dom';
const { Header, Content, Footer, Sider } = Layout;

import {
    MenuOutlined,
    MenuUnfoldOutlined,
    MenuFoldOutlined
} from '@ant-design/icons';

const Sidebar = ({ children }) => {
    const items = [
        {
            key: '1',
            label: <Link to="/">Home</Link>,
        },
        {
            key: '2',
            label: <Link to="/education">Education</Link>,
        },
        {
            key: '3',
            label: <Link to="/experience">Experience</Link>,
        },
        {
            key: '4',
            label: <Link to="/honours">Honours</Link>,
        },
        {
            key: '5',
            label: <Link to="/projects">Projects</Link>,
        },
        {
            key: '6',
            label: <Link to="/research">Research</Link>,
        },
        {
            key: '7',
            label: <Link to="/contact">Contact</Link>,
        },
    ]

    const icons = [
        {
            key: '1',
            icon: <Image src="cf.jpg" height="20px" width="20px" preview={false} />,
            onClick: () => { window.open('https://codeforces.com/profile/Iftekhar_Hakim_K', '_blank', 'noopener,noreferrer'); }
        },
        {
            key: '2',
            icon: <Image src="linkedin.jpg" height="20px" width="20px" preview={false} />,
            onClick: () => { window.open('https://www.linkedin.com/in/iftekharhakimk/', '_blank', 'noopener,noreferrer'); }
        },
        {
            key: '3',
            icon: <Image src="github.jpg" height="20px" width="20px" preview={false} />,
            onClick: () => { window.open('https://github.com/IftekharHakimK', '_blank', 'noopener,noreferrer'); }
        },
        {
            key: '4',
            icon: <Image src="icpc.png" height="20px" width="20px" preview={false} />,
            onClick: () => { window.open('https://icpc.global/ICPCID/NU59MNSTICCQ', '_blank', 'noopener,noreferrer'); }
        },
    ]
    const [isMobile, setIsMobile] = useState(window.innerWidth <= 576);
    const handleResize = () => {
        setIsMobile(window.innerWidth <= 576);
    };
    useEffect(() => {
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    return <>
        {isMobile ?
            <Layout>

                <div style={{ backgroundColor: '#E0F7F9', paddingTop: '5px', paddingBottom: '50px', minHeight: '100vh' }}>

                    <div style={{ backgroundColor: '#FFFFFF', margin: '10px', padding: '5px', marginTop: '20px', 
                        boxShadow: '0 0 10px rgba(0, 0, 0, 0.5)', borderRadius: '5px' }}>
                        <div style={{ display: 'flex', justifyContent: 'flex-end', marginRight: '15px' }}>
                            <Popover trigger="click" content={
                                <>
                                <Menu theme="light" mode="vertical" items={items} inlineIndent={2} />
                                <Menu theme="light" mode="horizontal" items={icons} />
                                </>
                            }>
                                { !isMenuOpen && <MenuUnfoldOutlined style={{fontSize: '25px'}} rotate={180} onClick={()=>setIsMenuOpen(true)}/> }
                                { isMenuOpen && <MenuFoldOutlined style={{fontSize: '25px'}} rotate={180} onClick={()=>setIsMenuOpen(false)}/> }
                            </Popover>
                        </div>
                        <Divider style={{marginTop: '8px'}}/>
                        {/* <Menu theme="light" mode="horizontal" items={items} inlineIndent={2} overflowedIndicator={<><MenuOutlined /></>} style={{minWidth: '10px'}}/> */}
                        <div style={{ backgroundColor: '#FFFFFF', margin: '2px', padding: '5px', marginTop: '2px' }}>
                            {children}
                        </div>
                    </div>
                </div>

            </Layout>
            :
            <div style={{ backgroundColor: '#E0F7F9', paddingTop: '50px', paddingBottom: '50px', minHeight: '100vh' }}>
                <div style={{ backgroundColor: '#FFFFFF', marginLeft: '10%', marginRight: '10%', borderRadius: '10px', boxShadow: '0 0 10px rgba(0, 0, 0, 0.5)' }}>
                    <Space.Compact direction="horizontal" style={{ backgroundColor: 'white', width: '100%', display: 'flex', justifyContent: 'space-evenly' }}>
                        <div style={{ width: '70vw' }}>
                            <Menu theme="light" mode="horizontal" items={items} inlineIndent={2} />
                        </div>
                        <div style={{ width: '30vw' }}>
                            <Menu theme="light" mode="horizontal" items={icons} />
                        </div>


                    </Space.Compact>
                    <div style={{ paddingLeft: '5%', paddingRight: '5%', paddingTop: '15px', paddingBottom: '50px', minHeight: '500px' }}>
                        {children}
                    </div>
                </div>
            </div>


        }

    </>
}
export default Sidebar;